import { Injectable } from '@angular/core';
import { IDefinitionAdapter } from './definition-adapter.interface';

@Injectable({
  providedIn: 'root'
})
export class RxDefinitionAdapterRegistryService {
  private runtimeAdapters: Map<string, IDefinitionAdapter> = new Map();
  private designAdapters: Map<string, IDefinitionAdapter> = new Map();

  registerRuntimeAdapter(type: string, adapter: IDefinitionAdapter): void {
    this.runtimeAdapters.set(type, adapter);
  }

  registerDesignAdapter(type: string, adapter: IDefinitionAdapter): void {
    this.designAdapters.set(type, adapter);
  }

  getRuntimeAdapter(type: string): IDefinitionAdapter {
    return this.runtimeAdapters.get(type);
  }

  getDesignAdapter(type: string): IDefinitionAdapter {
    return this.designAdapters.get(type);
  }
}
